.wrapper {
  margin-bottom: 1rem;
}

.input {
  border: 1px solid #eee;
  display: block;
  width: 100%;
  font-size: 1rem;
  padding: 0.8rem;
  border-radius: 0.4rem;
  background: #eee;
  transition: 0.2s;
}

.input:focus,
.input:hover {
  outline: none;
  border-color: #346C92;
  background: white;
  box-shadow: 0 0 0 3px #85bee4;
}

.label {
  display: block;
  font-size: 1rem;
  line-height: 1;
  padding-bottom: 0.5rem;
  color: #C5CBD9;
}

.error {
  color:  #85bee4;
  font-size: 0.875rem;
  margin-top: 0.25rem;
}
